import React, { Component } from "react";
import { Form, InputNumber, Button } from "antd";
import { Row, Col } from "antd";
import { HomeOutlined } from "@ant-design/icons";
import { Breadcrumb } from "antd";
import { Select } from "antd";
import { Steps, message } from "antd";
import { Card } from "antd";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import { Tag } from "antd";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import { Statistic } from "antd";
import { Modal } from "antd";
import { Switch, notification } from "antd";
// import Alert from "@material-ui/lab/Alert";
import { Alert } from "antd";

const { Step } = Steps;

class Livetrade extends Component {
  constructor(props) {
    super(props);
    this.state = {
      exchange: undefined,
      button: undefined,
      runstrategy: false,
      current: 0,
      setCurrent: 0,
      slot: 10,
      isModalVisible: false,
      auto: false,
    };
  }

  onExchangeChange = (value) => {
    console.log(`selected ${value}`);
    this.setState({ exchange: value });
  };

  onButtonChange = () => {
    console.log("button");
    this.setState({ button: "divide" });
  };
  onStrategyChange = () => {
    // console.log("button");
    this.setState({ runstrategy: "strategy" });
  };
  showModal = () => {
    this.setState({ isModalVisible: true });
  };
  handleOk = (values) => {
    console.log("ModalSuccess:", values);
    this.setState({ isModalVisible: false });
  };

  handleCancel = () => {
    this.setState({ isModalVisible: false });
  };

  onSwitchChange = (checked) => {
    console.log(`switch to ${checked}`);

    const close = () => {
      this.setState({ auto: true });
      console.log(
        "Notification was closed. Either the close button was clicked or duration time elapsed."
      );
    };

    if (checked == true) {
      const key = `open${Date.now()}`;
      const btn = (
        <Button
          type="primary"
          size="small"
          onClick={() => {
            this.setState({ auto: true });
            notification.close(key);
            console.log(`switch to ${checked}`);
          }}
        >
          Confirm
        </Button>
      );

      notification.open({
        // message: "Notification Title",
        description: (
          <Alert
            message="Warning"
            description="This is a warning notice about going Live"
            type="warning"
            showIcon
            // closable
          />
        ),
        btn,
        key,
        onClose: close,
      });
    }
  };

  render() {
    const steps = [
      {
        title: "First",
        content: "first",
      },
      {
        title: "Second",
        content: "Second-content",
      },
      {
        title: "Last",
        content: "Last-content",
      },
    ];
    const children = [];
    const data = [];
    for (let i = 1; i <= this.state.slot; i++) {
      children.push(i - 1);
      data.push({
        title: i,
        entry: i,
        size: i,
        ltp: i,
        pnl: i,
        tradetoken: i,
      });
    }
    function createData(date, symbol, amount, pnl) {
      return { date, symbol, amount, pnl };
    }

    const rows = [
      createData("21/07/2020", "NIFTI", 12000, 45.8),
      createData("21/07/2020", "NIFTI", 12000, 45.8),
      createData("21/07/2020", "NIFTI", 12000, 45.8),
      createData("21/07/2020", "NIFTI", 12000, 45.8),
      createData("21/07/2020", "NIFTI", 12000, 45.8),
    ];

    const validateMessages = {
      required: "${label} is required!",
      types: {
        email: "${label} is not a valid email!",
        number: "${label} is not a valid number!",
      },
      number: {
        range: "${label} must be between ${min} and ${max}",
      },
    };
    const onFinish = (values) => {
      console.log(values);
      message.success("Processing complete!");
    };
    const onCapitalChange = (value) => {
      console.log("changed", value);
    };
    const onSlotChange = (value) => {
      this.setState({ slot: value });
    };
    const onClick = () => {
      message.success("Processing complete!");
      this.setState({ runstrategy: true });
    };

    const nfo = [
      '"ACC"',
      '"ADANIENT"',
      '"ADANIPORTS"',
      '"AMARAJABAT"',
      '"AMBUJACEM"',
      '"APOLLOHOSP"',
      '"APOLLOTYRE"',
      '"ASHOKLEY"',
      '"ASIANPAINT"',
      '"AUROPHARMA"',
      '"AXISBANK"',
      '"BAJAJ-AUTO"',
      '"BAJAJFINSV"',
      '"BAJFINANCE"',
      '"BALKRISIND"',
      '"BANDHANBNK"',
      '"BANKBARODA"',
      '"BATAINDIA"',
      '"BEL"',
      '"BERGEPAINT"',
      '"BHARATFORG"',
      '"BHARTIARTL"',
      '"BHEL"',
      '"BIOCON"',
      '"BOSCHLTD"',
      '"BPCL"',
      '"BRITANNIA"',
      '"CADILAHC"',
      '"CANBK"',
      '"CHOLAFIN"',
      '"CIPLA"',
      '"COALINDIA"',
      '"COFORGE"',
      '"COLPAL"',
      '"CONCOR"',
      '"CUMMINSIND"',
      '"DABUR"',
      '"DIVISLAB"',
      '"DLF"',
      '"DRREDDY"',
      '"EICHERMOT"',
      '"ESCORTS"',
      '"EXIDEIND"',
      '"FEDERALBNK"',
      '"GAIL"',
      '"GLENMARK"',
      '"GMRINFRA"',
      '"GODREJCP"',
      '"GODREJPROP"',
      '"GRASIM"',
      '"HAVELLS"',
      '"HCLTECH"',
      '"HDFC"',
      '"HDFCBANK"',
      '"HDFCLIFE"',
      '"HEROMOTOCO"',
      '"HINDALCO"',
      '"HINDPETRO"',
      '"HINDUNILVR"',
      '"IBULHSGFIN"',
      '"ICICIBANK"',
      '"ICICIGI"',
      '"ICICIPRULI"',
      '"IDEA"',
      '"IDFCFIRSTB"',
      '"IGL"',
      '"INDIGO"',
      '"INDUSINDBK"',
      '"INFRATEL"',
      '"INFY"',
      '"IOC"',
      '"ITC"',
      '"JINDALSTEL"',
      '"JSWSTEEL"',
      '"JUBLFOOD"',
      '"KOTAKBANK"',
      '"L&TFH"',
      '"LICHSGFIN"',
      '"LT"',
      '"LUPIN"',
      '"M&M"',
      '"M&MFIN"',
      '"MANAPPURAM"',
      '"MARICO"',
      '"MARUTI"',
      '"MCDOWELL-N"',
      '"MFSL"',
      '"MGL"',
      '"MINDTREE"',
      '"MOTHERSUMI"',
      '"MRF"',
      '"MUTHOOTFIN"',
      '"NATIONALUM"',
      '"NAUKRI"',
      '"NESTLEIND"',
      '"NMDC"',
      '"NTPC"',
      '"ONGC"',
      '"PAGEIND"',
      '"PEL"',
      '"PETRONET"',
      '"PFC"',
      '"PIDILITIND"',
      '"PNB"',
      '"POWERGRID"',
      '"PVR"',
      '"RAMCOCEM"',
      '"RBLBANK"',
      '"RECLTD"',
      '"RELIANCE"',
      '"SAIL"',
      '"SBILIFE"',
      '"SBIN"',
      '"SHREECEM"',
      '"SIEMENS"',
      '"SRF"',
      '"SRTRANSFIN"',
      '"SUNPHARMA"',
      '"SUNTV"',
      '"TATACHEM"',
      '"TATACONSUM"',
      '"TATAMOTORS"',
      '"TATAPOWER"',
      '"TATASTEEL"',
      '"TCS"',
      '"TECHM"',
      '"TITAN"',
      '"TORNTPHARM"',
      '"TORNTPOWER"',
      '"TVSMOTOR"',
      '"UBL"',
      '"ULTRACEMCO"',
      '"UPL"',
      '"VEDL"',
      '"VOLTAS"',
      '"WIPRO"',
      '"ZEEL"',
    ];

    const next = () => {
      this.setState({ current: this.state.current + 1 });
    };

    const prev = () => {
      this.setState({ current: this.state.current - 1 });
    };
    const { Option } = Select;

    const onModalFinish = (values) => {
      console.log("ModalSuccess:", values);
      this.setState({ isModalVisible: false });
    };

    const FirstData = (value) => {
      console.log("hello");
      if (value == 0) {
        return (
          <>
            <Form.Item
              // style={{ fontWeight: "bolder" }}
              name={["user", "Strategy"]}
              label="Strategy"
              // rules={[{ required: true, message: "" }]}
            >
              <Select
                showSearch
                style={{ width: 150 }}
                placeholder="Strategy"
                optionFilterProp="children"
                filterOption={(input, option) =>
                  option.children.toLowerCase().indexOf(input.toLowerCase()) >=
                  0
                }
              >
                {["Strategy 1"].map((element) => {
                  return <Option value={element}> {element}</Option>;
                })}
              </Select>
            </Form.Item>
          </>
        );
      }
      if (value == 1) {
        return (
          <div>
            <Row>
              <Col span={12}>
                <Button style={{ backgroundColor: "#9ECB35" }} shape="round">
                  Set capital after
                </Button>
              </Col>
              <Col span={12}>
                <Button
                  style={{ backgroundColor: "#9ECB35" }}
                  onClick={this.onButtonChange}
                  shape="round"
                >
                  Divide the capital into slots
                </Button>
              </Col>
            </Row>
            {this.state.button == "divide" && (
              <div>
                <Form.Item
                  name={["user", "capital"]}
                  label="Capital"
                  rules={[{ required: true }]}
                >
                  <InputNumber
                    size="small"
                    min={1}
                    max={10000000}
                    onChange={onCapitalChange}
                  />
                </Form.Item>

                <Form.Item
                  name={["user", "leverage"]}
                  label="Leverage"
                  rules={[{ required: true }]}
                >
                  <Select
                    showSearch
                    style={{ width: 150 }}
                    placeholder="Leverage"
                    optionFilterProp="children"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .indexOf(input.toLowerCase()) >= 0
                    }
                  >
                    {["2", "3", "4"].map((element) => {
                      return <Option value={element}> {element}</Option>;
                    })}
                  </Select>
                </Form.Item>
                <Form.Item
                  name={["user", "slots"]}
                  label="slots"
                  rules={[{ required: true }]}
                >
                  <Select
                    showSearch
                    style={{ width: 150 }}
                    placeholder="Slots"
                    optionFilterProp="children"
                    onChange={onSlotChange}
                  >
                    {["1", "2", "3", "4", "5"].map((element) => {
                      return <Option value={element}> {element}</Option>;
                    })}
                  </Select>
                </Form.Item>
              </div>
            )}
          </div>
        );
      }
      if (value == 2) {
        return (
          <>
            <Form.Item name="exchange" label="Exchange">
              <Select
                showSearch
                style={{ width: 150 }}
                placeholder="Exchange"
                optionFilterProp="children"
                onChange={this.onExchangeChange}
                filterOption={(input, option) =>
                  option.children.toLowerCase().indexOf(input.toLowerCase()) >=
                  0
                }
              >
                {["NSE", "NFO"].map((element) => {
                  return <Option value={element}> {element}</Option>;
                })}
              </Select>
            </Form.Item>
            <Form.Item name="company" label="Company">
              {this.state.exchange == "NSE" ? (
                <Select
                  showSearch
                  style={{ width: 150 }}
                  placeholder="Company"
                  optionFilterProp="children"
                  filterOption={(input, option) =>
                    option.children
                      .toLowerCase()
                      .indexOf(input.toLowerCase()) >= 0
                  }
                >
                  {nfo.map((element) => {
                    return <Option value={element}> {element}</Option>;
                  })}
                </Select>
              ) : (
                <Select
                  showSearch
                  style={{ width: 150 }}
                  placeholder="Company"
                  optionFilterProp="children"
                  filterOption={(input, option) =>
                    option.children
                      .toLowerCase()
                      .indexOf(input.toLowerCase()) >= 0
                  }
                >
                  {["NSE", "NFO"].map((element) => {
                    return <Option value={element}> {element}</Option>;
                  })}
                </Select>
              )}
            </Form.Item>
            <Form.Item
              name="OrderType"
              label="Order Type"
              rules={[{ required: true }]}
            >
              <Select
                showSearch
                style={{ width: 150 }}
                placeholder="Order Type"
                optionFilterProp="children"
              >
                {["market order", "limit order", "stopless order"].map(
                  (element) => {
                    return <Option value={element}> {element}</Option>;
                  }
                )}
              </Select>
            </Form.Item>

            <Form.Item
              name="product"
              label="Product"
              rules={[{ required: true }]}
            >
              <Select
                showSearch
                style={{ width: 150 }}
                placeholder="Product"
                optionFilterProp="children"
              >
                {["CNC", "MIS"].map((element) => {
                  return <Option value={element}> {element}</Option>;
                })}
              </Select>
            </Form.Item>
            <Form.Item
              name="golive"
              label="Go Live"
              // rules={[{ required: true }]}
            >
              <Switch onChange={this.onSwitchChange} />
            </Form.Item>
          </>
        );
      }
    };

    return (
      <div>
        <Breadcrumb style={{ margin: "16px 0" }}>
          <Breadcrumb.Item href="">
            <HomeOutlined /> <span>Home</span>
          </Breadcrumb.Item>
          <Breadcrumb.Item>Deployment</Breadcrumb.Item>
          <Breadcrumb.Item>Livetrade</Breadcrumb.Item>
        </Breadcrumb>
        {/* <Row>
          <Col> */}
        {!this.state.runstrategy && (
          <Card hoverable style={{ width: 700 }}>
            <Steps current={this.state.current}>
              {steps.map((item) => (
                <Step key={item.title} title={item.title} />
              ))}
            </Steps>
            <Form
              name="nest-messages"
              onFinish={onFinish}
              validateMessages={validateMessages}
            >
              <div className="steps-content">
                {FirstData(this.state.current)}
              </div>
              <div className="steps-action">
                {this.state.current === steps.length - 1 && (
                  <Button
                    type="primary"
                    // htmlType="submit"
                    onClick={onClick}
                  >
                    Run Strategy
                  </Button>
                )}

                {this.state.current < steps.length - 1 &&
                  this.state.current == 0 && (
                    <Button type="primary" onClick={() => next()}>
                      Deploy Strategy
                    </Button>
                  )}
                {this.state.current < steps.length - 1 &&
                  this.state.current != 0 && (
                    <Button type="primary" onClick={() => next()}>
                      Next
                    </Button>
                  )}

                {this.state.current > 0 && (
                  <Button style={{ margin: "0 8px" }} onClick={() => prev()}>
                    Previous
                  </Button>
                )}
              </div>
            </Form>
          </Card>
        )}

        {this.state.runstrategy == true && (
          <>
            <Card hoverable style={{ width: 1200 }}>
              <Row>
                <Col span={6}>
                  <Statistic
                    title="Capital"
                    value={12000}
                    valueStyle={{ color: "#3f8600" }}
                  />
                </Col>
                <Col span={6}>
                  <Statistic
                    title="Strategy"
                    value={"Strategy 1 "}
                    precision={"running"}
                    valueStyle={{ color: "#3f8600" }}
                  />
                </Col>
                <Col span={6}>
                  <Statistic
                    title="Leverage"
                    value={2}
                    valueStyle={{ color: "#3f8600" }}
                  />
                </Col>
                <Col span={6}>
                  <Statistic
                    title="Slots"
                    value={4}
                    valueStyle={{ color: "#3f8600" }}
                  />
                </Col>
              </Row>
            </Card>

            <Row>
              <Col span={16}>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <Grid container justify="center" spacing={2}>
                      {children.map((value) => (
                        <Grid key={value} item>
                          <Card
                            hoverable
                            style={{ width: 200 }}
                            title={data[value].title}
                          >
                            <Tag color="#2db7f5" style={{ width: 80 }}>
                              LTP :
                            </Tag>
                            {data[value].ltp}
                            <br />
                            <Tag color="#87d068" style={{ width: 80 }}>
                              LotSize :
                            </Tag>
                            {data[value].size}
                            <br />
                            <Tag color="#108ee9" style={{ width: 80 }}>
                              EntryValue :
                            </Tag>
                            {data[value].entry}
                            <br />
                            <Tag color="gold" style={{ width: 80 }}>
                              Profit/loss :
                            </Tag>
                            {data[value].pnl}
                            <br />
                            <Tag color="purple" style={{ width: 80 }}>
                              Token :
                            </Tag>
                            {data[value].tradetoken}
                            <div>
                              {/* <Tag color="purple">
                                LTP :{data[value].ltp}
                                <br />
                                LotSize :{data[value].size}
                                <br />
                                EntryValue :{data[value].entry}
                                <br />
                                Profit/loss :{data[value].pnl}
                                <br />
                                Token :{data[value].tradetoken}
                              </Tag> */}
                              <Button
                                size="medium"
                                type="primary"
                                onClick={this.showModal}
                                disabled={
                                  this.state.auto == true ? true : false
                                }
                              >
                                Place Order
                              </Button>
                            </div>
                          </Card>
                          <Paper />
                        </Grid>
                      ))}
                    </Grid>
                  </Grid>
                </Grid>

                <Modal
                  title="Place order"
                  visible={this.state.isModalVisible}
                  onOk={this.handleOk}
                  onCancel={this.handleCancel}
                >
                  <Form name="basic" onFinish={onModalFinish}>
                    <Form.Item
                      name="OrderType"
                      label="Order Type"
                      rules={[{ required: true }]}
                    >
                      <Select
                        showSearch
                        style={{ width: 150 }}
                        placeholder="Order Type"
                        optionFilterProp="children"
                      >
                        {["market order", "limit order", "stopless order"].map(
                          (element) => {
                            return <Option value={element}> {element}</Option>;
                          }
                        )}
                      </Select>
                    </Form.Item>

                    <Form.Item
                      name="product"
                      label="Product"
                      rules={[{ required: true }]}
                    >
                      <Select
                        showSearch
                        style={{ width: 150 }}
                        placeholder="Product"
                        optionFilterProp="children"
                      >
                        {["CNC", "MIS"].map((element) => {
                          return <Option value={element}> {element}</Option>;
                        })}
                      </Select>
                    </Form.Item>
                  </Form>
                </Modal>
              </Col>
              <Col span={8}>
                <TableContainer component={Paper}>
                  <Table aria-label="simple table">
                    <TableHead>
                      <TableRow>
                        <TableCell>Date</TableCell>
                        <TableCell align="right">Symbol</TableCell>
                        <TableCell align="right">amount</TableCell>
                        <TableCell align="right">PNL</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {rows.map((row) => (
                        <TableRow key={row.date}>
                          <TableCell component="th" scope="row">
                            {row.date}
                          </TableCell>
                          <TableCell align="right">{row.symbol}</TableCell>
                          <TableCell align="right">{row.amount}</TableCell>
                          <TableCell align="right">{row.pnl}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
              </Col>
            </Row>
          </>
        )}
        {/* </Col>
        </Row> */}
      </div>
    );
  }
}

export default Livetrade;
